.error {
  width: 100%;
  text-align: center;
  color: #ff6060;
  padding: 4rem 1rem;
  font-family: "Montserrat", sans-serif;
}
.error__code {
  font-size: 10rem;
  font-weight: 700;
  margin: 0;
}
.error__message {
  font-size: 1.5rem;
  margin: 2rem 0;
}
.error__link {
  font-size: 1.125rem;
  color: #ff6060;
  text-decoration: underline;
}